body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f0f8ff;
  color: #333;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #4caf50;
  color: white;
  padding: 20px;
  font-size: 24px;
  text-transform: uppercase;
}

.App-header h1 {
  margin: 0;
  font-size: 36px;
}

.App-header p {
  margin-top: 10px;
  font-size: 18px;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.chart-container {
  width: 80%;
  max-width: 600px;
  margin: 20px 0;
}

.images {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.image {
  width: 300px;
  height: auto;
  border: 2px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.image:hover {
  transform: scale(1.05);
}

.App-footer {
  background-color: #333;
  color: white;
  padding: 10px;
  font-size: 14px;
  position: fixed;
  width: 100%;
  bottom: 0;
  text-align: center;
}